<template>
    <div id="em">
        <el-container>
            <el-header>欢迎您:{{username}}注册美多商城，请激活您的邮箱</el-header>
            <el-main>
                <el-link type="danger" :underline="false" @click.native='get_activation()'>欢迎注册美多商城，请点击该链接进行邮箱</el-link>
            </el-main>
        </el-container>
    </div>
</template>

<script>

                import Cookies from 'js-cookie'




export default {
    data(){
        return{
            username:Cookies.get('username'),
        }
    },
    methods:{
        get_activation:function(){
            let url = this.tornadoUrl+'/users/send';
            let username = Cookies.get('username');
            let user_id = Cookies.get('user_id');
            console.log(username)
            console.log(user_id)
            let _this = this;
            this.axios.post(url,this.qs.stringify({user_id:user_id})).then(res=>{
                if (res.data['code'] == 200){
                    _this.$router.push('/')
                }
            })
        }
    }
}
</script>

<style>
    .el-header, .el-footer {
    background-color: #E6A23C;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
</style>



