// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

/*引入img_url全局变量*/
import imgUrl from './components/Globals'
Vue.prototype.imgUrl = imgUrl.imgUrl;

/*引入tornadoUrl全局变量*/
import tornadoUrl from './components/Globals'
Vue.prototype.tornadoUrl = tornadoUrl.tornadoUrl;

import baseUrl from './components/Basurl'
Vue.prototype.baseUrl = baseUrl.baseUrl;

/*引入axios*/
import Axios from 'axios'
Vue.prototype.axios = Axios;

import QS from 'qs'
Vue.prototype.qs = QS;

/*引入公共js*/
import common from './common.js'
Vue.prototype.common = common

/* 导入cookie */
import Cookies from 'js-cookie'
Vue.prototype.jscookie = Cookies


/*引入样式*/
require('!style-loader!css-loader!./assets/styles/bootstrap-4.1.3/bootstrap.css');
require('!style-loader!css-loader!./assets/plugins/font-awesome-4.7.0/css/font-awesome.min.css');
require('!style-loader!css-loader!./assets/styles/main_styles.css');
require('!style-loader!css-loader!./assets/styles/responsive.css');

/* 引入elementUi */
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'


Vue.config.productionTip = false

/* 使用elementui */
Vue.use(ElementUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



