<template>


        <div class="super_container">

                <myheader :totalNumber="total"></myheader>
            
                <!-- Home -->
            
								
								<!-- Home -->

	<div class="home">
		<div class="parallax_background parallax-window" data-parallax="scroll" ></div>
		<div class="home_container">
			<div class="home_content">
				<div class="home_title">Shop</div>
				<div class="breadcrumbs">
					<ul class="d-flex flex-row align-items-center justify-content-start">
						<li><a href="index.html">Home</a></li>
						<li><a href="categories.html">Woman</a></li>
						<li><a href="categories.html">Accessories</a></li>
						<li>Shoulder Bag</li>
					</ul>
				</div>
			</div>
		</div>
	</div>

	<!-- Products -->

	<div class="product">

		<!-- Sorting & Filtering -->
		<div class="products_bar">
			<div class="section_container">
				<div class="container">
					<div class="row">
						<div class="col">
							<div class="products_bar_content d-flex flex-row align-items-center justify-content-start">
								<div class="product_categories">
									<ul class="d-flex flex-row align-items-start justify-content-start flex-wrap">
										<li class="active"><a href="#">All</a></li>
										<li><a href="#">Hot Products</a></li>
										<li><a href="#">New Products</a></li>
										<li><a href="#">Sale Products</a></li>
									</ul>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>

		<!-- Product Content -->
		<div class="section_container">
			<div class="container">
				<div class="row">
					<div class="col">
						<div class="product_content_container d-flex flex-lg-row flex-column align-items-start justify-content-start">
							<div class="product_content order-lg-1 order-2">
								<div class="product_content_inner">
									<div class="product_image_row d-flex flex-md-row flex-column align-items-md-end align-items-start justify-content-start">
										<div class="product_image_1 product_image">
											<img :src="image_name+imgList[3].image" alt="">
										</div>
										<div class="product_image_2 product_image"><img :src="image_name+imgList[0].image" alt=""></div>
									</div>
									<div class="product_image_row">
										<div class="product_image_3 product_image"><img :src="image_name+imgList[1].image" alt=""></div>
									</div>
									<div class="product_image_row d-flex flex-md-row flex-column align-items-start justify-content-start">
										<div class="product_image_4 product_image"><img :src="image_name+imgList[2].image" alt=""></div>
										<div class="product_image_5 product_image"><img :src="image_name+imgList[3].image" alt=""></div>
									</div>

									{{goodsList['5']}}
								</div>
							</div>
							<div class="product_sidebar order-lg-2 order-1">
								<form action="#" id="product_form" class="product_form">
									<div class="product_name">{{goodsList[0]}}</div>
									<div class="product_price">${{goodsList[2]}}</div>
									<div class="product_color">Color: <span>Brown</span></div>
									<div class="product_size">
										<div class="product_size_title">Select Size</div>
										<div class="product_size_list">
											<ul>
												<li class="size_available">
													<input type="radio" id="radio_1" name="product_radio" class="regular_radio radio_1">
													<label for="radio_1">35</label>
												</li>
												<li class="size_available">
													<input type="radio" id="radio_2" name="product_radio" class="regular_radio radio_2">
													<label for="radio_2">36</label>
												</li>
												<li class="size_available">
													<input type="radio" id="radio_3" name="product_radio" class="regular_radio radio_3">
													<label for="radio_3">37</label>
												</li>
												<li class="size_available">
													<input type="radio" id="radio_4" name="product_radio" class="regular_radio radio_4">
													<label for="radio_4">38</label>
												</li>
												<li>
													<input type="radio" id="radio_5" name="product_radio" class="regular_radio radio_5" disabled>
													<label for="radio_5">39</label>
												</li>
												<li>
													<input type="radio" id="radio_6" name="product_radio" class="regular_radio radio_6" disabled>
													<label for="radio_6">40</label>
												</li>
											</ul>
										</div>
									</div>
									<button class="cart_button trans_200"><router-link to="" @click.native="addCart(goodsList)">添加到购物车</router-link></button>
									<div class="similar_products_button trans_200"><a href="categories.html">同类商品</a></div>
								</form>
								<div class="product_links">
									<ul class="text-center">
										<li><a href="#">See guide</a></li>
										<li><a href="#">Shipping</a></li>
										<li><a href="#">Returns</a></li>
									</ul>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>	
	</div>
	<div>
	  <table>
		  <tr>
			  <td>评论时间</td>
			  <td>评论内容</td>
			  <td>用户</td>
		  </tr>
		  <tr v-for="i in tableData">
			  <td>{{i.create_time}}</td>
			  <td v-html="i.content"></td>
			  <td>{{i.user_id}}</td>
		  </tr>
	  </table>
	</div>
	<div class='tinymce'>
		<h3>评论区</h3>
		<editor id='tinymce' v-model='tinymceHtml' :init='init'></editor>
			<el-row>
		    	<el-button @click="comment">提交</el-button>
			</el-row>
	</div> 
            -->
                
            
                <!-- Footer -->
            
                <myfooter :totalNumber="total"></myfooter>


            
            
            </div>

      
</template>
      
      
       
      <script>


        import myheader from './header.vue'
		import myfooter from './footer.vue'
		// import img_url from '../main/img_url'
		
		import tinymce from 'tinymce/tinymce'
		import 'tinymce/themes/silver/theme'
		import Editor from '@tinymce/tinymce-vue'
		import 'tinymce/plugins/image'
		import 'tinymce/plugins/link'
		import 'tinymce/plugins/code'
		import 'tinymce/plugins/table'
		import 'tinymce/plugins/lists'
		import 'tinymce/plugins/contextmenu'
		import 'tinymce/plugins/wordcount'
		import 'tinymce/plugins/colorpicker'
		import 'tinymce/plugins/textcolor'
		
     
      
      export default {
		name: 'tinymce',
        data () {
          return {
            msg: "这是一个变量",
			dataList:[],
			goodsList:[],
			tableData: [],
			imgList:[],
            clicked:0,
			upath: '',
			goods:[],
			imgUrl:this.imgUrl,
			image_name:'',
			result: '',
			uping:0,
			total:{
				'totalNum':0,
                'totalPrice':0,
                'totalUser':0,
			},
			tinymceHtml: '请输入内容',
	        init: {
				language_url: '/static/tinymce/langs/zh_CN.js',
				language: 'zh_CN',
				skin_url: '/static/tinymce/skins/ui/oxide',
				height: 300,
				width:800,
				plugins: 'link lists image code table colorpicker textcolor wordcount contextmenu',
				toolbar:
				'bold italic underline strikethrough | fontsizeselect | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist | outdent indent blockquote | undo redo | link unlink image code | removeformat',
				branding: false
			},
          }
        },
        components: {
			 Editor,
        'myheader':myheader,
		'myfooter':myfooter,
		
        },
        mounted:function(){

			this.common.get_menu();
			this.id = this.$route.query.id;
			this.getGoods();
			this.getCommentData_post();
			var data = JSON.parse(this.jscookie.get(localStorage.getItem('u_name')));
			this.dataList = data;
			this.total_num();
			this.total_user();
			
			
        },
        methods:{
            changetest(index){
                this.clicked = index;
			},
			getCommentData_post:function(){
				let url = this.tornadoUrl+"/mall/get_comment";
				let _this = this;
				this.axios.post(url,this.qs.stringify({'goods_id':this.id})).then(res=>{
					if(res.data.code == 200){
						console.log(res)
						_this.tableData = res.data.comments;
					}
				})
			},
			comment:function(){
				let url = this.tornadoUrl+"/mall/published_comment";
				var u_name = localStorage.getItem('u_name');
				var token = localStorage.getItem('token');
				let data = {'tinymceHtml':this.tinymceHtml,'goods_id':this.id,'gname':this.goodsList[0],'u_name':u_name,'token':token};
				this.axios.post(url,this.qs.stringify(data)).then(res=>{
					if(res.data.code == 200){
						this.tinymceHtml = ''
					}else{
						console.log(res.data.mes)
					}
				})
			},
			getGoods:function(){
				let url = this.tornadoUrl+'/mall/get_goods?id='+this.id;
				let _this = this;
				this.axios.get(url).then(res=>{
					if (res.data.code == 200){
						_this.goodsList = res.data.goods
						_this.imgList = res.data.img
						// 商品图片路径
						_this.image_name = this.imgUrl
						console.log(_this.image_name)
					}
				})
			},
			addCart:function(goods){
                if(localStorage.getItem('u_name')){
                    alert('添加购物车成功')
                    let url = this.tornadoUrl+'/mall/storage_redis';
                    let _this = this;
                    let name = goods[0];
                    let cartList = this.dataList;
                    let findgood = 0;
                    // 遍历购物车列表
                    for(let i=0,l=cartList.length;i<l;i++){
                        // 判断如果商品存在时 商品数量递增
                        if(name == cartList[i]['name']){
                            cartList[i]['num']++;
                            findgood = 1;
                            break
                            }
                        }
                    // 商品不存在时 创建一条新的购物车商品记录
                    if(findgood == 0){
                        cartList.push({'name':name,'num':1,'price':goods[2],'img':goods[3],'id':goods[1]})
                        this.dataList = cartList
                        }
                    // 将购物车列表存储到jscookie里，key为用户的u_name
                    this.jscookie.set(localStorage.getItem('u_name'),this.dataList,{expires:7});
                    this.total_num()
                    var cart_list = this.jscookie.get(localStorage.getItem('u_name'))
                    var u_name = localStorage.getItem('u_name')
                    var token = localStorage.getItem('token')
                    // 向后端发送cart数据存储到Redis中
                    this.axios.post(url,this.qs.stringify({'cart':cart_list,'u_name':u_name,'token':token}))
                }else{
                    alert('请登录')
                    this.$router.push('/login')
                }
                
            },
			total_num:function(){
				let num = 0
				let price = 0
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){
					num += cartList[i]['num']
					price += cartList[i]['price'] * cartList[i]['num']
				
				}
				this.total['totalNum'] = num
				this.total['totalPrice'] = price
            },
            total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
            },
        }
      }
      
      
      </script>
       
      <style>


			@import '../assets/styles/cart.css';
			@import '../assets/styles/cart_responsive.css';

			@import '../assets/styles/checkout.css';
			@import '../assets/styles/checkout_responsive.css';


			@import '../assets/styles/product.css';
			@import '../assets/styles/product_responsive.css';
      
      
      </style>


