<template>


  <div class="super_container">



      <!-- Home -->

      <!--包含头部和左侧菜单-->

      <myheader></myheader>



    <div class="boxes">
      <div style="width:300px">
        <el-form label-width="82px" :inline="true">
          <el-form-item label="账 号：">
            <el-input v-model="username"></el-input>
          </el-form-item>
          <el-form-item label="昵 称：">
            <el-input v-model="nick"></el-input>
          </el-form-item>
          <el-form-item label="密 码：">
            <el-input v-model="pwd1" show-password></el-input>
          </el-form-item>
          <el-form-item label="确认密码:">
            <el-input v-model="pwd2" show-password></el-input>
          </el-form-item>
          <el-form-item label="邮 箱：">
            <el-input v-model="email"></el-input>
          </el-form-item>
          <el-form-item label="手 机：">
            <el-input v-model="phone"></el-input>
          </el-form-item>
          <el-form-item label="验证码：">
            <el-input v-model="code" style="width:90px"></el-input>
          </el-form-item>
          <el-button  type="primary" class="code_btn" style="background: #fff;color: #000;border: 1px solid #999;float: right;margin-top: 10px;" @click.native.prevent="getMessageCode('formData')">
            <span v-if="!sendMsgDisabled">{{content}}</span>
            <span v-if="sendMsgDisabled">{{codeTime+'秒后获取'}}</span>
          </el-button>
          <el-row>
            <div>{{msg}}</div>
            <el-button v-on:click='getApiDate_post()' plain>注 册</el-button>
            　<el-link type="danger"><router-link to="/login">已有账号?点击登录</router-link></el-link>
          </el-row>
          
        </el-form>
      </div>
    </div>
  </div>
</template>



      <script>


        import Cookies from 'js-cookie' 
        import myheader from './header.vue'
        // import myfooter from './footer.vue'


      export default {
        data () {
          return {
            username:'',
            nick:'',
            pwd1:'',
            pwd2:'',
            email:'',
            phone:'',
            msg:'',
            user:'',
            region:'',
            code:'',
            msg:'',
            codeTime: 59, // 发送验证码倒计时
            sendMsgDisabled: false, // 是否显示获取验证码按钮
            content:'获取验证码',
          }
        },
        components: {
        'myheader':myheader,
        // 'myfooter':myfooter
        },
        mounted:function(){

            this.common.get_menu();



      },
        methods:{
            changetest(index){
                this.clicked = index;
            },
            getApiDate_post:function(){
              // 跨域请求注册地址
              var url = this.tornadoUrl+'/users/register';
              var _this = this;
              // 跨域发送post请求，参数
              this.axios.post(url,this.qs.stringify({
                username:this.username,pwd1:this.pwd1,pwd2:this.pwd2,email:this.email,phone:this.phone,nick:this.nick,code:this.code
                })).then(res=>{
                  console.log(res)
                  if(res.data['code'] == 200){
                    localStorage.setItem('u_name',this.username)
                    localStorage.setItem('token',res.data.token)
                    _this.$router.push('/')
                  }else {
                    _this.msg = res.data.message
                  }
                })
            },
            getMessageCode:function(){
              let url = this.tornadoUrl+'/users/getcode';
              let _this = this;
              this.axios.post(url,this.qs.stringify({
                phone:this.phone
              })).then(res=>{
                if(res.data['code'] == 200){
                  _this.sendMsgDisabled = true;
                  let codeInterval = window.setInterval(function() {
                      if ((_this.codeTime--) <= 0) {
                            _this.codeTime = 59;
                            _this.sendMsgDisabled = false;
                            window.clearInterval(codeInterval);
                            this.content = '超时重新发送'
                        }
                    }, 1000);
                }else {
                  _this.msg = res.data.message
                }
              })
              
            }
        },

      }


      </script>

      <style>



      </style>



