<template>


        <div class="super_container">

                <myheader :totalNumber="total"></myheader>
            
                <!-- Home -->
            
								
								<!-- Home -->

	<div class="home">
		<div class="parallax_background parallax-window" data-parallax="scroll" data-speed="0.8"></div>
		<div class="home_container">
			<div class="home_content">
				<div class="home_title">我的订单</div>
				<div class="breadcrumbs">
					<ul class="d-flex flex-row align-items-center justify-content-start">
						<li><a href="index.html">Home</a></li>
						<li><a href="cart.html">订单</a></li>
						<li>我的订单</li>
					</ul>
				</div>
			</div>
		</div>
	</div>

	<!-- Checkout -->

	<div class="checkout">
		<div class="section_container">
			<div class="container">
				<div class="row">
					<div class="col">
						<div class="checkout_container d-flex flex-xxl-row flex-column align-items-start justify-content-start">
							
						

							<!-- Cart Total -->
							<div class="cart_total">
								<div class="cart_total_inner checkout_box">
									<div class="checkout_title" v-if="orderList.ststes==1">支付成功</div>
									<div class="checkout_title" v-else-if="orderList.ststes==2">退款成功</div>
									<ul class="cart_extra_total_list">
										<li class="d-flex flex-row align-items-center justify-content-start">
											<div class="cart_extra_total_title">订单编号</div>
											<div class="cart_extra_total_value ml-auto">{{orderList.id}}</div>
										</li>
										<li class="d-flex flex-row align-items-center justify-content-start">
											<div class="cart_extra_total_title">邮递方式</div>
											<div class="cart_extra_total_value ml-auto">Free</div>
										</li>
										<li class="d-flex flex-row align-items-center justify-content-start">
											<div class="cart_extra_total_title">交易金额</div>
											<div class="cart_extra_total_value ml-auto">${{orderList.payment_amount}}</div>
										</li>
										<li class="d-flex flex-row align-items-center justify-content-start">
											<div class="cart_extra_total_title">订单时间</div>
											<div class="cart_extra_total_value ml-auto">{{orderList.create_time}}</div>
										</li>
										<li class="d-flex flex-row align-items-center justify-content-start">
											<div class="cart_extra_total_title">收货地址</div>
											<div class="cart_extra_total_value ml-auto">北京</div>
										</li>
									</ul>

									

									<!-- Order Text -->
									<div class="order_text">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin pharetra temp or so dales. Phasellus sagittis auctor gravida. Integ er bibendum sodales arcu id te mpus. Ut consectetur lacus.</div>

									<div class="checkout_button trans_200" v-if="orderList.ststes==1"><router-link to="" @click.native="getApiRefund()">点击退款</router-link></div>
									<div class="checkout_button trans_200"><router-link to="/">返回首页</router-link></div>
								</div>
							</div>

						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
            
                
            
                <!-- Footer -->
            
                <myfooter :totalNumber="total"></myfooter>


            
            
            </div>

      
</template>
      
      
       
      <script>


        import myheader from './header.vue'
        import myfooter from './footer.vue'
     
      
      export default {
        data () {
          return {
            msg: "这是一个变量",
			dataList:[],
			orderList:[],
			total:{
				'totalNum':0,
                'totalPrice':0,
                'totalUser':0,
			},
            clicked:0,
            upath: '',
			result: '',
			uping:0,
			order:'',
          }
        },
        components: {
        'myheader':myheader,
        'myfooter':myfooter
        },
        mounted:function(){

			this.common.get_menu();
			this.total_num();
			this.total_user();
			let ord = this.$route.query.ord;
			this.order = ord;
			this.getOrder();
        },
        methods:{
            changetest(index){
                this.clicked = index;
			},
			total_num:function(){
				let num = 0
				let price = 0
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){
					num += cartList[i]['num']
					price += cartList[i]['price'] * cartList[i]['num']
				
				}
				this.total['totalNum'] = num
				this.total['totalPrice'] = price
            },
            total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
			},
			getOrder:function(){
				let url = this.tornadoUrl+'/mall/get_order';
				let _this = this
				this.axios.post(url,this.qs.stringify({o_id:this.order})).then(res=>{
					if(res.data.code == 200){
						_this.orderList = res.data.orderList
					}
				})
			},
			getApiRefund:function(){
				let url = this.tornadoUrl+'/mall/refund';
				this.axios.post(url,this.qs.stringify({o_id:this.order}))
				// window.location.href = '/checkout'
			}
        }
      }
      
      
      </script>
       
      <style>


			

			@import '../assets/styles/checkout.css';
			@import '../assets/styles/checkout_responsive.css';
      
      
      
      </style>


