import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/index'
import element_test from '@/components/element_test'
import login from '@/components/login'
import Register from '@/components/Register'
import valid_email_needed from '@/components/valid_email_needed'
import cate from '@/components/cate'
import product from '@/components/product'
import cart from '@/components/cart'
import order from '@/components/order'
import checkout from '@/components/checkout'
import aaaaaa from '@/components/aaaaaa'

Vue.use(Router)


var routes = [
  {
    path: '/',
    name: 'index',
    component: Index
  },
  {
    path: '/element_test',
    name: 'element_test',
    component: element_test
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/valid_email_needed',
    name: 'valid_email_needed',
    component: valid_email_needed
  },
  {
    path: '/cate',
    name: 'cate',
    component: cate
  },
  {
    path: '/product',
    name: 'product',
    component: product
  },
  {
    path: '/cart',
    name: 'cart',
    component: cart
  },
  {
    path: '/order',
    name: 'order',
    component: order
  },
  {
    path: '/checkout',
    name: 'checkout',
    component: checkout
  },
  {
    path: '/aaaaaa',
    name: 'aaaaaa',
    component: aaaaaa
  },
]


export default new Router({
  routes: routes,
  mode:'history' /*hash*/
})



