<template>


  <div class="super_container">
          <!-- Home -->

          <!--包含头部和左侧菜单-->

          <myheader :totalNumber="total"></myheader>



        <div class="boxes">
          <div style="width:300px">
            <el-form label-width="80px">
              {{ msg }}
              <el-form-item label="账 号：">
                <el-input v-model="username" placeholder="请输入账号" ></el-input>
              </el-form-item>
              <el-form-item label="密 码：">
                <el-input v-model="pwd" placeholder="请输入密码" type="password" show-password ></el-input>
              </el-form-item>
              <el-row>
                <el-button v-on:click='getApiData_post()'>登录</el-button>
                <el-link type="danger"><router-link to="/register">还没有账号?点击注册</router-link></el-link> 
              </el-row>
              <el-row>
                <el-tooltip class="item" effect="dark" content="微博登录" placement="top-start">
                <a href="" ><img src="../assets/images/微信图片_20190729160221.jpg" alt="" width="40px" height="40px" v-on:click="getApiWeibo_get()"></a>
                </el-tooltip>

                <el-tooltip class="item" effect="dark" content="QQ登录" placement="top-start">
                <router-link to=""><img src="../assets/images/Connect_logo_1.png" alt="" v-on:click="getApiQq_get()"></router-link>
                </el-tooltip>

              </el-row>
            </el-form>
          </div>
        </div>
        <myfooter :totalNumber="total"></myfooter>
      </div>
</template>



      <script>


        import Cookies from 'js-cookie'
        import myheader from './header.vue'
        import myfooter from './footer.vue'


      export default {
        data () {
          return {
            dataList:[],
            username:'',
            pwd:'',
            msg:'',
            urls:'',
            total:{
				      'totalNum':0,
              'totalPrice':0,
              'totalUser':0,
			},
          }
        },
        components: {
        'myheader':myheader,
        'myfooter':myfooter
        },
        mounted:function(){

            this.common.get_menu();
            this.total_num();
            this.total_user();
            var data = JSON.parse(this.jscookie.get('cart'));
            this.dataList = data;

      },
        methods:{
            changetest(index){
                this.clicked = index;
            },
            register(){
                this.$router.push({})
            },
            getApiData_post:function(){
              // 定义跨域请求登陆地址
              var url = this.tornadoUrl+'/users/login';
              var _this = this;
              // 发送post请求，相应成功后会执行then方法中的回调函数
              this.axios.post(url,this.qs.stringify({u_name:this.username,pwd:this.pwd})).then(res=>{
                console.log(res)
                  // 判断返回的code值是否为200；200=成功
                  if(res.data['code'] == 200){
                    localStorage.setItem('u_name',this.username)
                    localStorage.setItem('token',res.data.token)
                    _this.$router.push('/')
                    // 返回的code值不为200时获取返回信息
                  }else {
                    _this.msg = res.data.message
                  }
            })
          },
          getApiWeibo_get:function(){
            let url = this.tornadoUrl+'/users/sinafirsthandler';
            let _this = this;
            this.axios.get(url).then(res=>{
              if (res.data['code'] == 200){
                console.log(res)
                _this.urls = res.data.auth_url
                window.location.href = res.data.auth_url
              }
            })
          },
          total_num:function(){
            let num = 0
            let price = 0
            let cartList = this.dataList;
            for(let i=0,l=cartList.length;i<l;i++){
              num += cartList[i]['num']
              price += cartList[i]['price'] * cartList[i]['num']
            
              } 
            this.total['totalNum'] = num
            this.total['totalPrice'] = price
            },
            total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
            },
            getApiQq_get:function(){
              let url = this.tornadoUrl + '/get_authorization_code';
              // let _this = this;
              console.log('2222222')
              this.axios.get(url).then(res=>{
                console.log(res.data['url'])
                window.location.href = res.data['url']
              })
            },
        },
        

      }


      </script>

      <style>



      </style>



