<template>


        <div class="super_container">

                <myheader :totalNumber="total"></myheader>
            
                <!-- Home -->
            
                
            
                <!-- Boxes -->
                <div class="tlinks">Collect from <a href="http://www.cssmoban.com/" title="网站模板">网站模板</a></div>
                <div class="boxes">
                    <div class="section_container">
                        <div class="container">
                            <div class="row">
                                
                                <!-- Box -->
                                <div class="col-lg-4 box_col" v-for="cate in cateList">
                                    <div class="box">
                                        <div class="box_image"><img src="../assets/images/box_1.jpg" alt=""></div>
                                        <div class="box_title trans_200"><a :href='"/cate?id=" + cate.id'>{{cate['cname']}}</a></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            
                <!-- Categories -->
            
                <div class="categories">
                    <div class="section_container">
                        <div class="container">
                            <div class="row">
                                <div class="col text-center">
                                    <div class="categories_list_container">
                                        <ul class="categories_list d-flex flex-row align-items-center justify-content-start">
                                            <li><a href="categories.html">new arrivals</a></li>
                                            <li><a href="categories.html">recommended</a></li>
                                            <li><a href="categories.html">best sellers</a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            
                <!-- Products -->
            
                <div class="products">
                    <div class="section_container">
                        <div class="container">
                            <div class="row">
                                <div class="col">
                                    <div class="products_container grid">
                                        
                                        <!-- Product -->
                                        <div class="product grid-item hot" v-for="goods in goodsList" v-if="goods[4]==1">
                                            <div class="product_inner">
                                                <div class="product_image">
                                                    <img :src='img_url+goods[3]' alt="">
                                                    <div class="product_tag">hot</div>
                                                </div>
                                                <div class="product_content text-center">
                                                    <div class="product_title"><router-link :to='"/product?id="+goods[1]'>{{goods[0]}}</router-link></div>
                                                    <div class="product_price">${{goods[2]}}</div>
                                                    <div class="product_button ml-auto mr-auto trans_200"><router-link to="/" @click.native="addCart(goods)">添加到购物车</router-link></div>
                                                </div>
                                            </div>	
                                        </div>
            
                                    </div>
                                    <page v-if="changestatus" :dataAll="all" :dataCur="cur" :datanum="num" :dataDatanum="dataNum" @change="pageChange"></page>
                                </div>
                                
                            </div>
                            
                        </div>
                        
                    </div>
                </div>
            
                <!-- Footer -->
                  
                <myfooter :totalNumber="total"></myfooter>


            
            
            </div>

      
</template>
      
      
       
      <script>


        import myheader from './header.vue'
        import myfooter from './footer.vue'
        import page from './page.vue'
     
      
      export default {
        data () {
          return {
            msg: "这是一个变量",
            cateList:[],
            goodsList:[],
            dataList:[],
            clicked:0,
            upath: '',
            result: '',
            uping:0,
            totalPrice:0,
            img_url:'',
            total:{
				'totalNum':0,
                'totalPrice':0,
                'totalUser':0,
            },
            all: 0, //总页数
            cur: 1, //当前页码
            num: 3, //一页显示的数量 必须是奇数
            dataNum: 0, //数据的数量
            changestatus:false
            }
        },
        components: {
        'myheader':myheader,
        'myfooter':myfooter,
        'page':page,
        },
        mounted:function(){

            this.common.get_menu();
            this.get_good_sinfo();
            var data = JSON.parse(this.jscookie.get(localStorage.getItem('u_name')));
            this.dataList = data;
            this.total_num();
            this.total_user();
            let u_name = this.$route.query.u_name;
            if(u_name){
                localStorage.setItem('u_name',u_name)
                let token = this.$route.query.token;
                localStorage.setItem('token',token)
            }
            
        },
        methods:{
            changetest(index){
                this.clicked = index;
            },
            get_good_sinfo:function(){
                let url = this.tornadoUrl+'/mall/index';
                let _this = this;
                this.axios.post(url,this.qs.stringify({'cur':_this.cur,'num':_this.num})).then(res=>{
                    if (res.data['code'] == 200){
                        console.log(res)
                        _this.cateList = res.data.cateList
                        _this.goodsList = res.data.goodsList
                        _this.dataNum = res.data.count
                        _this.all = Math.ceil(res.data.count/_this.num)
                        _this.changestatus = true
                        this.img_url = this.imgUrl
                    }
                })
            },
            pageChange: function(page) {
                this.cur = page
                this.get_good_sinfo()
                
            },
            addCart:function(goods){
                if(localStorage.getItem('u_name')){
                    alert('添加购物车成功')
                    let url = this.tornadoUrl+'/mall/storage_redis';
                    let _this = this;
                    let name = goods[0];
                    let cartList = this.dataList;
                    let findgood = 0;
                    // 遍历购物车列表
                    for(let i=0,l=cartList.length;i<l;i++){
                        // 判断如果商品存在时 商品数量递增
                        if(name == cartList[i]['name']){
                            cartList[i]['num']++;
                            findgood = 1;
                            break
                            }
                        }
                    // 商品不存在时 创建一条新的购物车商品记录
                    if(findgood == 0){
                        cartList.push({'name':name,'num':1,'price':goods[2],'img':goods[3],'id':goods[1]})
                        this.dataList = cartList
                        }
                    // 将购物车列表存储到jscookie里，key为用户的u_name
                    this.jscookie.set(localStorage.getItem('u_name'),this.dataList,{expires:7});
                    this.total_num()
                    var cart_list = this.jscookie.get(localStorage.getItem('u_name'))
                    var u_name = localStorage.getItem('u_name')
                    var token = localStorage.getItem('token')
                    // 向后端发送cart数据存储到Redis中
                    this.axios.post(url,this.qs.stringify({'cart':cart_list,'u_name':u_name,'token':token}))
                }else{
                    alert('请登录')
                    this.$router.push('/login')
                }
                
            },
            total_num:function(){
				let num = 0
				let price = 0
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){
					num += cartList[i]['num']
					price += cartList[i]['price'] * cartList[i]['num']
				
				}
				this.total['totalNum'] = num
				this.total['totalPrice'] = price
            },
            total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
            },
        }
      }
      
      
      </script>
       
      <style>
      
      
      
      </style>


