<template>


        <div class="super_container">

                <myheader :totalNumber="total"></myheader>
            
                <!-- Home -->
            
                
            
                <!-- Home -->

	<div class="home">
		<div class="parallax_background parallax-window" data-parallax="scroll"   ></div>
		<div class="home_container">
			<div class="home_content">
				<div class="home_title">Shop</div>
				<div class="breadcrumbs">
					<ul class="d-flex flex-row align-items-center justify-content-start">
						<li><a href="index.html">Home</a></li>
						<li><a href="#">Woman</a></li>
						<li>Accessories</li>
					</ul>
				</div>
			</div>
		</div>
	</div>

	<!-- Products -->

	<div class="products">

		<!-- Sorting & Filtering -->
		<div class="products_bar">
			<div class="section_container">
				<div class="container">
					<div class="row">
						<div class="col">
							<div class="products_bar_content d-flex flex-column flex-xxl-row align-items-start align-items-xxl-center justify-content-start">
								<div class="product_categories">
									<ul class="d-flex flex-row align-items-start justify-content-start flex-wrap">
										<li class="active"><a href="#">All</a></li>
										<li><a href="#">Hot Products</a></li>
										<li><a href="#">New Products</a></li>
										<li><a href="#">Sale Products</a></li>
									</ul>
								</div>
								<div class="products_bar_side ml-xxl-auto d-flex flex-row align-items-center justify-content-start">
									<div class="products_dropdown product_dropdown_sorting">
										<div class="isotope_sorting_text"><span>Default Sorting</span><i class="fa fa-caret-down" aria-hidden="true"></i></div>
										<ul>
											<li class="item_sorting_btn" data-isotope-option='{ "sortBy": "original-order" }'>Default</li>
											<li class="item_sorting_btn" data-isotope-option='{ "sortBy": "price" }'>Price</li>
											<li class="item_sorting_btn" data-isotope-option='{ "sortBy": "name" }'>Name</li>
										</ul>
									</div>
									<div class="product_view d-flex flex-row align-items-center justify-content-start">
										<div class="view_item active"><img src="../assets/images/view_1.png" alt=""></div>
										<div class="view_item"><img src="../assets/images/view_2.png" alt=""></div>
										<div class="view_item"><img src="../assets/images/view_3.png" alt=""></div>
									</div>
									<div class="products_dropdown text-right product_dropdown_filter">
										<div class="isotope_filter_text"><span>Filter</span><i class="fa fa-caret-down" aria-hidden="true"></i></div>
										<ul>
											<li class="item_filter_btn" data-filter="*">All</li>
											<li class="item_filter_btn" data-filter=".hot">Hot</li>
											<li class="item_filter_btn" data-filter=".new">New</li>
											<li class="item_filter_btn" data-filter=".sale">Sale</li>
										</ul>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="section_container">
			<div class="container">
				<div class="row">
					<div class="col">
						<div class="products_container grid">
							
							<!-- Product -->
							<div class="product grid-item hot" v-for="goods in goodsList" v-if="goods[4]==1">
								<div class="product_inner">
									<div class="product_image">
										<img :src="img_url+goods[3]" alt="">
										<div class="product_tag">hot</div>
									</div>
									<div class="product_content text-center">
										<div class="product_title"><a :href='"/product?id="+goods[1]'>{{goods[0]}}</a></div>
										<div class="product_price">${{goods[2]}}</div>
										<div class="product_button ml-auto mr-auto trans_200"><router-link to="" @click.native="addCart(goods)">添加到购物车</router-link></div>
									</div>
								</div>	
							</div>

							

						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
            
                <!-- Footer -->
				<page v-if="changestatus" :dataAll="all" :dataCur="cur" :datanum="num" :dataDatanum="dataNum" @change="pageChange"></page>
                <myfooter :totalNumber="total"></myfooter>


            
            
            </div>

      
</template>
      
      
       
      <script>


        import myheader from './header.vue'
		import myfooter from './footer.vue'
		import page from './page.vue'
     
      
      export default {
        data () {
          return {
            msg: "这是一个变量",
			dataList:[],
			goodsList:[],
            clicked:0,
            upath: '',
			result: '',
			uping:0,
			img_url:'',
			total:{
				'totalNum':0,
                'totalPrice':0,
                'totalUser':0,
			},
			all: 0, //总页数
            cur: 1, //当前页码
            num: 1, //一页显示的数量 必须是奇数
            dataNum: 0, //数据的数量
            changestatus:false
          }
        },
        components: {
        'myheader':myheader,
		'myfooter':myfooter,
		'page':page,
        },
        mounted:function(){
			
			this.common.get_menu();
			// 在钩子种获取index传过来的分类id
			this.id = this.$route.query.id;
			// 在钩子中获取搜索内容关键字
			this.content = this.$route.query.c;
			
			this.getGoods_List();
			var data = JSON.parse(this.jscookie.get(localStorage.getItem('u_name')));
			this.dataList = data;
			this.total_num();
			this.total_user();
        },
        methods:{
            changetest(index){
                this.clicked = index;
			},
			getGoods_List:function(){
				let url = this.tornadoUrl+'/mall/get_goods_list';
				let surl = this.tornadoUrl+'/mall/search_content?c=' + this.content;
				let _this = this;
				if(this.id){
					this.axios.post(url,this.qs.stringify({id:this.id,'cur':_this.cur,'num':_this.num})).then(res=>{
					if (res.data.code == 200){
						console.log(res)
						_this.goodsList = res.data.goods_list
						_this.dataNum = res.data.count
                        _this.all = Math.ceil(res.data.count/_this.num)
                        _this.changestatus = true
						this.img_url = this.imgUrl
						console.log(this.img_url)
						}
					})
				}else{
					this.axios.get(surl).then(res=>{
						if(res.data.code == 200){
							_this.goodsList = res.data.goodsList
							this.img_url = this.imgUrl
							console.log(res,'-----------------------')
						}
					})
				}
			},
			 pageChange: function(page) {
                this.cur = page
                this.getGoods_List()   
            },
			addCart:function(goods){
                if(localStorage.getItem('u_name')){
                    alert('添加购物车成功')
                    let url = this.tornadoUrl+'/mall/storage_redis';
                    let _this = this;
                    let name = goods[0];
                    let cartList = this.dataList;
                    let findgood = 0;
                    // 遍历购物车列表
                    for(let i=0,l=cartList.length;i<l;i++){
                        // 判断如果商品存在时 商品数量递增
                        if(name == cartList[i]['name']){
                            cartList[i]['num']++;
                            findgood = 1;
                            break
                            }
                        }
                    // 商品不存在时 创建一条新的购物车商品记录
                    if(findgood == 0){
                        cartList.push({'name':name,'num':1,'price':goods[2],'img':goods[3],'id':goods[1]})
                        this.dataList = cartList
                        }
                    // 将购物车列表存储到jscookie里，key为用户的u_name
                    this.jscookie.set(localStorage.getItem('u_name'),this.dataList,{expires:7});
                    this.total_num()
                    var cart_list = this.jscookie.get(localStorage.getItem('u_name'))
                    var u_name = localStorage.getItem('u_name')
                    var token = localStorage.getItem('token')
                    // 向后端发送cart数据存储到Redis中
                    this.axios.post(url,this.qs.stringify({'cart':cart_list,'u_name':u_name,'token':token}))
                }else{
                    alert('请登录')
                    this.$router.push('/login')
                }
                
            },
			total_num:function(){
				let num = 0
				let price = 0
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){
					num += cartList[i]['num']
					price += cartList[i]['price'] * cartList[i]['num']
				
				}
				this.total['totalNum'] = num
				this.total['totalPrice'] = price
			},
			total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
            }
        }
      }
      
      
      </script>
       
      <style>
      
            @import '../assets/styles/categories.css';
			@import '../assets/styles/categories_responsive.css';
      
      </style>


