<template>


        <div class="super_container">

                <myheader :totalNumber="total"></myheader>
            
                <!-- Home -->
            
								
								<!-- Home -->

	<div class="home">
		<div class="parallax_background parallax-window" data-parallax="scroll" ></div>
		<div class="home_container">
			<div class="home_content">
				<div class="home_title">订单</div>
				<div class="breadcrumbs">
					<ul class="d-flex flex-row align-items-center justify-content-start">
						<li><a href="index.html">Home</a></li>
						<li>订单</li>
					</ul>
				</div>
			</div>
		</div>
	</div>

	<!-- Cart -->

	<div class="cart_section">
		<div class="section_container">
			<div class="container">
				<div class="row">
					<div class="col">
						<div class="cart_container">
							
							<!-- Cart Bar -->
							<div class="cart_bar">
								<ul class="cart_bar_list item_list d-flex flex-row align-items-center justify-content-start">
									<li>商品</li>
									<li>单价</li>
									<li>数量</li>
									<li>小计</li>
								</ul>
							</div>

							<!-- Cart Items -->
							<div class="cart_items" v-for="(goods,index) in dataList">
								<ul class="cart_items_list">

									<!-- Cart Item -->
									<li class="cart_item item_list d-flex flex-lg-row flex-column align-items-lg-center align-items-start justify-content-start">
										<div class="product d-flex flex-lg-row flex-column align-items-lg-center align-items-start justify-content-start">
											<div><div class="product_image"><img :src="img_url+goods.img" alt=""></div></div>
											<div class="product_name"><a href="product.html">{{goods['name']}}</a></div>
										</div>
									
										<div class="product_price text-lg-center product_text"><span>Price: </span>${{goods['price']}}</div>
										<div class="product_quantity_container">
											<div class="product_quantity ml-lg-auto mr-lg-auto text-center">
												<span class="product_text product_num">{{goods['num']}}</span>
												<!-- <div class="qty_sub qty_button trans_200 text-center"><router-link to="" @click.native="minus(index)"><span>-</span></router-link></div>
												<div class="qty_add qty_button trans_200 text-center"><router-link to="" @click.native="add(index)"><span>+</span></router-link></div> -->
											</div>
										</div>
										<div class="product_total text-lg-center product_text"><span>Total: </span>${{goods['price']*goods['num']}}</div>
									</li>
								</ul>
							</div>

							<!-- Cart Buttons -->
							<!-- <div class="cart_buttons d-flex flex-row align-items-start justify-content-start">
								<div class="cart_buttons_inner ml-auto d-flex flex-row align-items-start justify-content-start flex-wrap">
									<div class="button button_continue trans_200"><a href="categories.html">continue shopping</a></div>
									<div class="button button_clear trans_200"><a href="categories.html">clear cart</a></div>
									<div class="button button_update trans_200"><a href="categories.html">update cart</a></div>
								</div>
							</div> -->
						</div>
					</div>
				</div>
			</div>
		</div>

		<div class="section_container cart_extra_container">
			<div class="container">
				<div class="row">

					<!-- Shipping & Delivery -->
					<div class="col-xxl-6">
						<div class="cart_extra cart_extra_1">
							<div class="cart_extra_content cart_extra_coupon">
								<div class="cart_extra_title">优惠码</div>
								<div class="coupon_form_container">
									<form action="#" id="coupon_form" class="coupon_form">
										<input type="text" class="coupon_input" required="required" v-model="code">
										<button class="coupon_button" v-on:click='getApiData_get()'>使用优惠码</button>
									</form>
								</div>
								<div class="shipping">
									<div class="cart_extra_title">配送方式</div>
									<ul>
										<li class="shipping_option d-flex flex-row align-items-center justify-content-start">
											<label class="radio_container">
												<input type="radio" id="radio_1" name="shipping_radio" class="shipping_radio">
												<span class="radio_mark"></span>
												<span class="radio_text">Next day delivery</span>
											</label>
											<div class="shipping_price ml-auto">$4.99</div>
										</li>
										<li class="shipping_option d-flex flex-row align-items-center justify-content-start">
											<label class="radio_container">
												<input type="radio" id="radio_2" name="shipping_radio" class="shipping_radio">
												<span class="radio_mark"></span>
												<span class="radio_text">Standard delivery</span>
											</label>
											<div class="shipping_price ml-auto">$1.99</div>
										</li>
										<li class="shipping_option d-flex flex-row align-items-center justify-content-start">
											<label class="radio_container">
												<input type="radio" id="radio_3" name="shipping_radio" class="shipping_radio" checked>
												<span class="radio_mark"></span>
												<span class="radio_text">Personal Pickup</span>
											</label>
											<div class="shipping_price ml-auto">Free</div>
										</li>
									</ul>
								</div>
							</div>
						</div>
					</div>

					<!-- Cart Total -->
					<div class="col-xxl-6">
						<div class="cart_extra cart_extra_2">
							<div class="cart_extra_content cart_extra_total">
								<div class="cart_extra_title">Order Total</div>
								<ul class="cart_extra_total_list">
									<li class="d-flex flex-row align-items-center justify-content-start">
										<div class="cart_extra_total_title">应付金额</div>
										<!-- <div class="cart_extra_total_value ml-auto">${{totalPrice() == tp?totalPrice(): tp =totalPrice()}}</div> -->
                                        <div class="cart_extra_total_value ml-auto">${{tPrice}}</div>
									</li>
									<li class="d-flex flex-row align-items-center justify-content-start">
										<div class="cart_extra_total_title">Shipping</div>
										<div class="cart_extra_total_value ml-auto">Free</div>
									</li>
									<li class="d-flex flex-row align-items-center justify-content-start">
										<div class="cart_extra_total_title">实付金额</div>
										<div class="cart_extra_total_value ml-auto" v-if="discounts == 0">${{tPrice}}</div>
                                        <div class="cart_extra_total_value ml-auto" v-else>${{discounts}}</div>
									</li>
								</ul>
								<div class="checkout_button trans_200"><router-link to="" @click.native="getApiPay_post()">确定支付</router-link></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
            
                
            
                <!-- Footer -->
            
                <myfooter :totalNumber="total"></myfooter>


            
            
            </div>

      
</template>
      
      
       
      <script>


        import myheader from './header.vue'
        import myfooter from './footer.vue'
     
      
      export default {
        data () {
          return {
            msg: "这是一个变量",
            dataList:[],
            code:'',
            discounts:0,
            clicked:0,
            upath: '',
            tPrice:0,
			result: '',
			uping:0,
			totalMoney:0,
			tp:0,
			img_url:'',
			total:{
				'totalNum':0,
                'totalPrice':0,
                'totalUser':0,
			},
			
          }
        },
        components: {
        'myheader':myheader,
        'myfooter':myfooter
        },
        mounted:function(){

			this.common.get_menu();
			var data = JSON.parse(this.jscookie.get(localStorage.getItem('u_name')));
			this.dataList = data
			
			this.img_url = this.imgUrl;
            this.total_num();
            this.total_user();
            this.totalPrice()
        },
        methods:{
            changetest(index){
                this.clicked = index;
			},
			
			
			// 遍历购物车列表，计算总价
			totalPrice:function(){
				// let tPrice = 0;
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){

                    this.tPrice += cartList[i]['price'] * cartList[i]['num']
                    console.log(this.tPrice,'------------')
					// tPrice.toFixed(2);

				}
				
			},
			// 钩子函数计算购物车中所有商品数量、价格
			total_num:function(){
				let num = 0
				let price = 0
				let cartList = this.dataList;
				for(let i=0,l=cartList.length;i<l;i++){
					num += cartList[i]['num']
					price += cartList[i]['price'] * cartList[i]['num']
				
				}
				this.total['totalNum'] = num
				this.total['totalPrice'] = price
            },
            // 第三方支付宝支付
            getApiPay_post:function(){
                let url = this.tornadoUrl+'/page';
                let _this = this;
                // 获取jscookie购物车列表
                let cartList = JSON.stringify(this.dataList);
                let u_name = localStorage.getItem('u_name');
                let token = localStorage.getItem('token');
                if(u_name){
                    this.axios.post(url,this.qs.stringify({'cartList':cartList,'code':this.code,token:token,u_name:u_name})).then(res=>{
                        let pay_url = res.data.pay_url
                        this.jscookie.remove(localStorage.getItem('u_name'))
                        window.location.href = pay_url
                    })
                }else{
                    alert('请登录')
                    this.$router.push('/login')
                }
                
            },
            // 获取优惠码
            getApiData_get:function(){
                let url = this.tornadoUrl+'/mall/coupons';
                let _this = this
                let dis = 0;
                let u_name = localStorage.getItem('u_name');
                let token = localStorage.getItem('token');
                if(u_name){
                    this.axios.post(url,this.qs.stringify({code:this.code,token:token,u_name:u_name})).then(res=>{
                        if(res.data.code == 200){
                            dis = res.data.discount * this.tPrice
                            _this.discounts = Math.round(dis)
                        }
                    })
                }else{
                    alert('请先登录')
                }
                
            },
            total_user:function(){
                let url = this.tornadoUrl+'/mall/total_user';
                this.axios.get(url).then(res=>{
                    if(res.data.code == 200){
                        this.total.totalUser = res.data.total_user
                        console.log(this.total.totalUser)
                    }
                })
            },
            
        }
      }
      
      
      </script>
       
      <style>
			
			@import '../assets/styles/cart.css';
			@import '../assets/styles/cart_responsive.css';
      
      
      </style>


